// state module
import produce from "immer";

// actionTypes
const CHANGE_STATE = "state/CHANGE_STATE" as const;

// action creator
export const changeState = (state: string) => {
    return {
        type: CHANGE_STATE,
        payload: state,
    };
};

//     
type StateAction = ReturnType<typeof changeState>;

// reducer
//    
type StateState = {
    currentState: string;
};

const initialState: StateState = {
    currentState: "",
};

export default function reducer(state: StateState = initialState, action: StateAction): StateState {
    return produce(state, (draft) => {
        switch (action.type) {
            case CHANGE_STATE:
                draft.currentState = action.payload;
                break;
            default:
                break;
        }
    });
}
